<template>
  <div>
    <LayoutBox one="订单列表" two="订单详情">
      <stepsCmp :order-list-data="orderListData"></stepsCmp>

      <div class="order_status">
        <h4>订单状态</h4>
        <p v-if="orderListData.orderStatus === 0">买家已下单，等待买家付款</p>
        <p v-else-if="orderListData.orderStatus === 1">买家已付款，等待卖家发货</p>
        <p v-else-if="orderListData.orderStatus === 2">卖家已发货，等待买家收货</p>
        <p v-else-if="orderListData.orderStatus === 3">卖家已收货，等待卖家评论</p>
        <p v-else-if="orderListData.orderStatus === 4">买家发起退款中</p>
        <p v-else-if="orderListData.orderStatus === 9">交易关闭</p>
        <p v-else-if="orderListData.orderStatus === 10">买家已评价，交易完成</p>
        <el-button class="express" type="text" @click="onExpress">查看物流详情</el-button>
      </div>

      <div class="buyer_infor">
        <div class="buyer_infor_bottom"><h4>买家信息</h4></div>
        <div class="buyer_infor_box">
          <span>用户昵称：<span>{{ orderListData.userName }}</span></span>
          <!-- 0微信 1支付宝 -->
          <span>
            支付方式：
            <span>{{ orderListData.payType === 2 ? '微信' : orderListData.payType === 1 ? '支付宝' : '暂未支付' }}</span>
          </span>
          <span>
            收货地址：
            <span>{{ orderListData.receiverName }}<span>{{ orderListData.receiverPhone }}</span></span>
          </span>
          <span class="address">{{ orderListData.receiverAddress }}</span>
        </div>
      </div>

      <div class="order_details">
        <div class="order_details_bottom"><h4>订单详情</h4></div>
        <div class="text_box">
          <span>订单编号：<span>{{ orderListData.orderCode }}</span></span>
          <span>下单时间：<span>{{ orderListData.createTime }}</span></span>
          <span>付款时间：<span>{{ orderListData.payTime }}</span></span>
        </div>
      </div>
      <el-table class="goods_table" :data="orderData">
        <el-table-column label="商品" width="250" align="center">
          <template slot-scope="scope">
            <div class="goods_table_goods">
              <el-image :src="scope.row.prodUrl" fit="cover" style="width: 70px; height: 70px"></el-image>
              <span class="goods_name">{{ scope.row.prodName }}</span>
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="itemSpec" label="商品属性" width="120" align="center"></el-table-column>
        <el-table-column label="状态" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.status === 0" style="color: #40ccae">待付款</span>
            <span v-else-if="scope.row.status === 1" style="color: #e39025">待发货</span>
            <span v-else-if="scope.row.status === 2" style="color: #49ca26">已发货</span>
            <span v-else-if="scope.row.status === 3" style="color: #c5fd68">待评价</span>
            <span v-else-if="scope.row.status === 4" style="color: #e02929">退款中</span>
            <span v-else-if="scope.row.status === 10" style="color: #666666">已完成</span>
            <span v-else-if="scope.row.status === 9" style="color: #999999">已关闭</span>
            <span v-else-if="orderListData.orderStatus === 0" style="color: #40ccae">待付款</span>
            <span v-else-if="orderListData.orderStatus === 1" style="color: #e39025">待发货</span>
            <span v-else-if="orderListData.orderStatus === 2" style="color: #49ca26">已发货</span>
            <span v-else-if="orderListData.orderStatus === 3" style="color: #c5fd68">待评价</span>
            <span v-else-if="orderListData.orderStatus === 4" style="color: #e02929">退款中</span>
            <span v-else-if="orderListData.orderStatus === 10" style="color: #666666">已完成</span>
            <span v-else-if="orderListData.orderStatus === 9" style="color: #999999">已关闭</span>
          </template>
        </el-table-column>
        <el-table-column label="单价" align="center">
          <template slot-scope="scope">
            <span>￥{{ scope.row.price }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="count" label="数量" align="center"></el-table-column>
        <el-table-column prop="discountPrice" label="总优惠" align="center">
          <template>
            <span>￥{{ orderListData.totalDiscountPrice }}</span>
          </template>
        </el-table-column>
        <el-table-column label="商品总价" align="center">
          <template>
            <span>￥{{ orderListData.totalPrice }}</span>
          </template>
        </el-table-column>
      </el-table>

      <div class="pay_money">
        <div>
          <!-- 订单状态 0-待付款 1-待发货 2-待收货 3-已签收/待评价 4-退款中 5-待拼团 9-交易关闭 10-交易完成 小超状态0-代付款10-完成 -->
          <el-button
            v-if="orderListData.orderStatus == 0"
            class="inquire"
            style="width: 100px"
            type="success"
            size="small"
            round
          >提醒付款</el-button>
          <el-button
            v-if="orderListData.orderStatus == 0"
            style="width: 100px"
            size="small"
            round
            @click="closeOrder"
          >关闭订单</el-button>
          <el-button
            v-if="orderListData.orderStatus == 1"
            class="inquire"
            style="width: 100px"
            type="success"
            size="small"
            round
            @click="onShip"
          >去发货</el-button>
          <el-button
            v-if="orderListData.orderStatus == 2"
            class="inquire"
            style="width: 100px"
            type="success"
            size="small"
            round
            @click="onLodop"
          >打印运单</el-button>
          <el-button
            v-if="orderListData.orderStatus == 1"
            style="width: 100px"
            size="small"
            round
            @click="onRequestRefund"
          >申请退款</el-button>
          <el-button
            v-if="orderListData.orderStatus == 2"
            class="inquire"
            style="width: 100px"
            type="success"
            size="small"
            round
            @click="onOrderRecOvertime"
          >延迟收货时间</el-button>
        </div>
        <div>
          <span>实付款：</span>
          <span class="money">{{ orderListData.payAmount }}</span>
          <span>元</span>
        </div>
      </div>
    </LayoutBox>
    <el-dialog title="退款原因填写" :visible.sync="dialogFormVisible">
      <el-form ref="form" :model="form" :rules="rules">
        <el-form-item label="退款金额" label-width="120px" prop="price">
          <el-input v-model="form.price" class="price_input"></el-input>
          <span class="price_remarks">建议退款￥{{ orderListData.payAmount }}</span>
        </el-form-item>
        <el-form-item label="退款原因" label-width="120px" prop="returnReason">
          <el-input v-model="form.returnReason" class="return_reason_input"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitForm('form')">确 定</el-button>
      </div>
    </el-dialog>

    <lodopCmp ref="lodop" />
  </div>
</template>

<script>
import stepsCmp from '@/views/order/order_content/steps_cmp/index.vue'
import LayoutBox from '@/components/LayoutBox/index'
import { MIXINS_FORMRULE } from '@/mixins/form.js'
import { MIXINS_ORDERHEADFINDBYID_API } from '@/mixins/api.js'
import lodopCmp from '@/components/lodop_cmp/index.vue'

export default {
  name: 'OrderContent',

  components: {
    stepsCmp, LayoutBox, lodopCmp,
  },

  data() {
    return {
      pageNo: 1, // 订单详情下的的商品分页当前页数默认1第一页
      orderHeaderId: '', // 订单id
      orderData: [], // 订单明细数据
      orderListData: {}, // 订单列表数据
      dialogFormVisible: false, // 打开退货的表单
      form: {}, // 退货的表单
      rules: {
        price: [
          { required: true, message: '请输入退款金额', trigger: 'blur' }
        ],
        returnReason: [
          { required: true, message: '请输入退款原因', trigger: 'blur' }
        ]
      }
    }
  },

  mixins: [
    MIXINS_ORDERHEADFINDBYID_API,
    MIXINS_FORMRULE
  ],

  created () {
    this.orderHeaderId = this.$route.query.id
  },

  mounted () {
    this._findOrderList()
    this._getOrderData()
  },

  methods: {
    // 关闭订单
    closeOrder () {
      this.$confirm('此操作将导致订单失效, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this._orderClose()
      })
    },
    // 点击去发货
    onShip () {
      this.$router.push({ path: '/orderList/orderContent/ship', query: { id: this.orderListData.id }})
    },
    // 点击延迟收货时间
    onOrderRecOvertime () {
      this.$confirm('是否延迟收货时间?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.apiOrderRecOvertime()
      })
    },
    // 点击申请退款
    onRequestRefund () {
      this.dialogFormVisible = true
    },
    // 点击退款里面的确定
    submitForm (formName) {
      let data = this.ruleForm(formName)
      this.apiOrderRefund(data)
    },
    // 点击查看物流
    onExpress () {
      let logisticsData = {
        logisticsStatus: this.orderListData.postType,
        wbNo: this.orderListData.logistcsNumber,
        expressCompanyName: this.orderListData.logisticsCompany,
        receiverName: this.orderListData.receiverName,
        receiverPhone: this.orderListData.receiverPhone,
        receiverAddress: this.orderListData.receiverAddress
      }
      this.$router.push({
        path: '/orderList/orderContent/expressDetail',
        query: { logisticsData: JSON.stringify(logisticsData) }
      })
    },
    // 点击打印
    onLodop () {
      this.orderHeadFindById(this.$route.query.id).then((res) => {
        console.log('查询订单', res)
        if (res.data.success) {
          this.$refs.lodop.printCode(res.data.data)
          this.$router.push({ path: '/orderList/orderContent', query: { id: this.$route.query.id }})
        } else {
          this.$message.error(res.data.msg)
        }
      })
    },

    // 接口api
    // 获取明细数据
    _getOrderData () {
      const DATA = {
        pageNo: this.pageNo,
        orderHeaderId: this.orderHeaderId
      }
      this.$http.post('/woho/order/item/query', DATA)
        .then((res) => {
          this.orderData = res.data.data.list
        })
    },
    // 根据id查询订单
    _findOrderList () {
      this.$http.post('/woho/order/head/findById/' + this.orderHeaderId)
        .then((res) => {
          this.orderListData = res.data.data
          console.log(res.data.data)
        })
    },
    // 订单关闭
    _orderClose () {
      this.$http.post('/woho/order/head/orderClose/' + this.orderHeaderId)
        .then((res) => {
          console.log(res.data.data)
          if (res.data.success) {
            this.$message.success('删除成功!')
          }
        })
    },
    // 订单收货延长时间
    apiOrderRecOvertime () {
      let data = {
        id: this.orderHeaderId
      }
      this.$http.post('/woho/order/head/orderRecOvertime', data)
        .then((res) => {
          if (res.data.success) {
            this.$message.success('已延迟一周')
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
    // 申请退款
    apiOrderRefund (formData) {
      let data = {
        type: 0,
        id: this.orderHeaderId,
        ...formData
      }
      this.$http.post('/woho/order/head/refundAll', data)
        .then((res) => {
          if (res.data.success) {
            this.$message.success('退款成功')
            this.$router.push('/orderList')
          } else {
            this.$message.error(res.data.msg)
          }
        })
    }
  }
}
</script>

<style lang="scss" scoped>
  $themColor: #5bd9be;

  .order_status{
    width: 90%;
    background-color: #e6fffc;
    border-radius: 5px;
    border: solid 1px $themColor;
    margin: 56px auto auto auto;
    padding-left: 25px;
    .express{
      color: $themColor;
      display: inline-block;
      margin: 15px 0px;
    }
  }
  .buyer_infor{
    width: 90%;
    margin: 25px auto auto auto;
    .buyer_infor_bottom{
      border-bottom: 1px solid #bfbfbf;
    }
    .buyer_infor_box{
      margin-top: 15px;
      font-size: 14px;
      display: flex;
      flex-direction: column;
      .address{
        margin-left: 70px;
      }
    }
  }
  .order_details{
    width: 90%;
    margin: 66px auto auto auto;
    .order_details_bottom{
      border-bottom: 1px solid #bfbfbf;
    }
    .text_box{
      margin-top: 15px;
      font-size: 14px;
      display: flex;
      justify-content: space-between;
      width: 70%;
    }
  }
  .goods_table{
      width: 90%;
      margin: 15px auto;
    .goods_table_goods{
      display: flex;
      align-items: center;
      .goods_name{
        width: 150px;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 3;
        overflow: hidden;
        text-align: left;
        margin: 5px;
      }
    }
  }
  .pay_money{
    font-size: 14px;
    font-weight: bold;
    width: 90%;
    margin: auto;
    display: flex;
    justify-content: space-between;
    .money{
      color: #f9930f;
    }
  }

  .price_input{
    width: 200px
  }
  .price_remarks{
    color: #999999;
    font-size: 7px;
    margin-left: 15px;
  }
  .return_reason_input{
    width: 200px
  }
</style>
